@font-face {
  font-family: "Pokemon-hollow";
  src: url("../fonts/pokemon-hollow.woff2") format("woff2"),
    url("../fonts/pokemon-hollow.woff") format("woff"),
    url("../fonts/pokemon-hollow.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "Pokemon-solid";
  src: url("../fonts/pokemon-solid.woff2") format("woff2"),
    url("../fonts/pokemon-solid.woff") format("woff"),
    url("../fonts/pokemon-solid.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}

.pokemon-font-solid {
  font-family: "Pokemon-solid", sans-serif;
  letter-spacing: 2px;
}

.pokemon-font-hollow {
  font-family: "Pokemon-hollow", sans-serif;
  letter-spacing: 2px;
}
