/*--------------------------------------------------------------
# Typography
--------------------------------------------------------------*/
@font-face {
  font-family: "Pokemon-hollow";
  src: url("../fonts/pokemon-hollow.woff2") format("woff2"), url("../fonts/pokemon-hollow.woff") format("woff"), url("../fonts/pokemon-hollow.ttf") format("truetype");
  font-weight: normal;
  font-style: normal; }

@font-face {
  font-family: "Pokemon-solid";
  src: url("../fonts/pokemon-solid.woff2") format("woff2"), url("../fonts/pokemon-solid.woff") format("woff"), url("../fonts/pokemon-solid.ttf") format("truetype");
  font-weight: normal;
  font-style: normal; }

.pokemon-font-solid {
  font-family: "Pokemon-solid", sans-serif;
  letter-spacing: 2px; }

.pokemon-font-hollow {
  font-family: "Pokemon-hollow", sans-serif;
  letter-spacing: 2px; }

/*--------------------------------------------------------------
# Header
--------------------------------------------------------------*/
body {
  background-color: #ef5350; }

header,
footer {
  background-color: #ef5350; }

.wordmark {
  max-width: 200px; }

/*--------------------------------------------------------------
# Content
--------------------------------------------------------------*/
body {
  font-family: "Lato", Roboto, "Helvetica Neue", sans-serif;
  height: 100%;
  position: relative; }
  body.pokedex, body.home, body.login, body.logout {
    background-image: linear-gradient(#ece3ff, #d5ddfe, #c8e0ff);
    background-attachment: fixed;
    min-height: 100vh; }
  body.home {
    min-height: 100vh; }
    body.home .btn {
      background-color: #ef5350;
      color: #ffcc03;
      width: 90%;
      max-width: 600px;
      font-size: 40px;
      height: 100px;
      display: flex;
      align-items: center;
      justify-content: center;
      text-shadow: -2px 2px black;
      -webkit-text-stroke-color: #1d497e;
      -webkit-text-stroke-width: 3px; }
      body.home .btn:hover, body.home .btn:focus {
        background-color: #ca4543; }
  body.pokemon-single {
    background-image: linear-gradient(#cdbde8, #8f99e7);
    background-attachment: fixed; }
    body.pokemon-single .pokemon-level {
      color: #fff;
      text-align: center;
      text-transform: uppercase;
      font-size: small;
      margin-top: 1em; }

#content {
  overflow: hidden;
  display: block;
  position: relative;
  padding-bottom: 55px;
  /* height of your footer */
  /* min-height: calc(100vh - 91px); /* will cover the 100% of viewport */ }

.pokemon-card {
  max-width: 100px; }

.back-link {
  position: fixed;
  bottom: 10px;
  margin-left: auto;
  margin-right: auto;
  left: 0;
  right: 0;
  width: 40px; }

.back-link svg {
  fill: #449b9d;
  width: 40px;
  background-color: rgba(255, 255, 255, 0.8);
  border-radius: 55px; }

@media screen and (min-width: 481px) {
  .back-link svg {
    box-shadow: 0px 0px 0px 4px rgba(255, 255, 255, 0.5); } }

.back-link svg:hover,
.back-link svg:focus,
.back-link svg:active {
  background-color: white; }

/* .pokemon img {
animation: Rotate 4s linear infinite;
    transform-origin: center;
} */
@keyframes Rotate {
  0%,
  100% {
    transform: rotateY(0deg); }
  25% {
    transform: rotateY(20deg); }
  75% {
    transform: rotateY(-20deg); } }

.pokemon-single .container {
  max-width: 45em; }

.pokemon-single .pokemon {
  padding-bottom: 50px; }

.pokemon-single .pokemon h1::before {
  content: "";
  background-image: url("../images/pokeball-icon.svg");
  height: 30px;
  width: 30px;
  display: inline-block;
  background-repeat: no-repeat;
  margin: 0 0.25em; }

.pokemon-pack > div:not(:last-child) {
  border-bottom: solid 1px rgba(0, 0, 0, 0.3); }

.pokemon hr {
  width: 70%;
  background: white; }

.evolution-chain span {
  font-size: 1.5em; }

body.pokedex img.lazy {
  height: 100px; }

@media screen and (max-width: 768px) {
  .pokemon .attributes {
    font-size: 0.7em; }
  body.pokedex img {
    max-height: 50px; } }

.font-weight-thin {
  font-weight: 100 !important; }

a.dimmed {
  pointer-events: none; }
  a.dimmed img {
    filter: contrast(0); }

.errors {
  color: var(--danger); }

/*--------------------------------------------------------------
# Footer
--------------------------------------------------------------*/
.pokemon-single footer {
  display: none; }

footer {
  position: absolute;
  bottom: 0;
  width: 100%; }
